#nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #1b2430;
  padding: 20px 50px;
  box-shadow: 0 5px 15px rgba(0 0 0 0.06);
}

#unorder {
  display: flex;
  justify-content: center;
  align-items: center;
}

#unorder li {
  padding: 0 10px;
  list-style: none;
  position: relative;
}

#unorder li a {
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  transition: 0.3s ease-in-out;
}

#unorder li a:hover,
#unorder li a.active {
  color: #17cf97;
}

#unorder li a:hover::after,
#unorder li a.active::after {
  content: "";
  width: 50%;
  height: 2px;
  background: #17cf97;
  position: absolute;
  bottom: -4px;
  left: 20px;
}

#mobile {
  display: none;
  color: #fff;
  cursor: pointer;
}

@media screen and (max-width: 769px) {
  #unorder {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    position: fixed;
    top: 90px;
    right: -300px;
    width: 300px;
    height: 100vh;
    background: #2a3239;
    box-shadow: 0 40px 60px rbba (0 0 0 0.1);
    padding: 40px 0 0 10px;
    transition: 0.3s ease-in-out;
  }
  #nav {
    margin: 0;
  }

  #unorder.active {
    right: 0px;
  }

  #mobile {
    display: block;
    color: #fff;
    cursor: pointer;
  }

  #unorder li {
    margin-bottom: 25px;
  }

  #unorder li a {
    font-size: 25px;
    cursor: pointer;
  }
  #heading {
    right: -0px;
    position: relative;
    transition: 0.3s ease-in-out;
  }
  #heading.active {
    left: -150px;
    transition: 0.3s ease-in-out;
  }
}
